* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 10px;
}

body {
  font-size: 1.6rem;
}
div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  /* background-color: #ddd; */
  /* color: #222; */
}

header {
  flex-basis: 30vh;
  /* background-color: #000; */
  /* color: white; */
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
  /* background-color: #555; */
}

aside {
  min-width: 240px;
  /* background-color: #999; */
}

section.page {
  flex-grow: 1;
  /* background-color: red; */
}

footer {
  display: flex;
  justify-content: center;
  background-color: #000;
  /* color: white; */
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
}
/* header po polaczeniu kompilacji webpackiem bedzie dostepny dla wszystkich styli */
