nav.main-nav {
    /* background-color: coral; */
    padding: 0 30px 30px 0;
}

.main-nav ul {
    list-style-type: none;
}

.main-nav a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: .2s;
}

.main-nav a:hover {
    transform: translateX(20px);
}

.main-nav a.active {
    border-left: 10px solid grey;
    transform: translateX(0);
}


.main-nav li:nth-child(1) a.active {
    border-left-color: green;
}

.main-nav li:nth-child(2) a.active {
    border-left-color: blue;
}

.main-nav li:nth-child(3) a.active {
    border-left-color: yellow;
}

.main-nav li:nth-child(4) a.active {
    border-left-color: red;
}
.products h2 {
    margin-bottom: 20px;
}

.products ul {
    list-style: square;
    padding-left: 21px;
}

.products li {
    margin-bottom: 20px;
}

.products a {
    text-decoration: none;
    color: #333;
}

.products a:hover {
    color: #000;
}
.contact textarea {
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    font-size: 1.3rem;
    line-height: 1.6;
    border: 1px solid #ddd;
    outline: 1px solid #ddd;
    font-family: open-sans, serif;
    font-weight: normal;
}

.contact button {
    display: block;
    padding: 10px 15px;
    border: 1px solid #444;
    margin: 0 20% 0 auto;
    cursor: pointer;
    outline: none;
    transition: .2s ease-in-out;
}

.contact button:hover {
    color: white;
    background-color: rgb(41, 40, 40);
}
.login-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 10px;
}

label {
    font-size: 1.4rem;
}

input {
    width: 50%;
    margin: 5px 0;
    border: none;
    border-bottom: 1px solid #ccc;
    outline: none;
}

button {
    width: 20%;
    margin-top: 20px;
    border: none;
    padding: 10px 15px;
    background-color: #ddd;
    cursor: pointer;
    outline: none;
    transition: .3s ease-in-out;
}

button:hover {
    background-color: rgb(61, 61, 61);
    color: white;
}
footer div {
    width: 25%
}

footer span {
    color: red;
}

footer p {
    font-size: 1rem;
    color: white;
}
